*{
    box-sizing: border-box;
}

body{
    font-family: 'Inter', sans-serif;
    background-color: #282d35;
}


nav {
    display: flex;
    align-items:center;
    background-color: #21222a;
    height: 9px;
    padding: 30px 25px;
}

.nav--logo_text, .nav--title {
    margin: 0;
}

.nav--logo_text {
    margin-right: auto;
    color: #61dafb;
    font-weight: 700;
    font-size: 22px;
}

.nav--title {
    color: #deebf8;
}

.nav--icon {
    height: 30px;
    margin-right: 7px;
}

main {
    padding: 57px 27px;
    color: white;
    background-image: url(./logo.svg);
    background-repeat: no-repeat;
    background-position: right 75%;
}

h1 {
    margin: 0;
}

.main--title{
    margin: 0;
    font-size: 39px;
    letter-spacing: -0.05em;
}

.main--facts{
    margin-top: 46px;
    max-width: 400px;
}

.main--facts > li {
    line-height: 19px;
    padding-block: 10px;
}

.main--facts > li::marker {
    color: #61dafb;
    font-size: 1.4rem;
}